.about {
  padding: 0 25px;
  width: 100vw;
  margin-bottom: 100px;
  flex-direction: column;

  @include for-normal-layout {
    @media (min-width: $narrow-breakpoint) {
      flex-direction: row;
    }
  }

  h3 {
    font-size: rem(18px);
    margin-bottom: 9px;
  }

  h1 {
    font-family: plex-semibold;
    // font-size: rem(28px);
    font-size: clamp(rem(32px), 5vw, rem(80px));
    margin-bottom: 32px;
    line-height: 1;
  }

  p {
    font-size: rem(14px);
    margin-bottom: 32px;
  }

  button {
    color: $clr2;
    font-size: rem(18px);
  }

  .actions {
    justify-content: space-between;
    margin-bottom: 20px;

    .container-button {
      position: relative;

      .mas {
        position: absolute;
        height: 47px;
        width: 156px;
        font-size: rem(18px);
        text-align: center;
        color: $clr1;
        background: $clr2;
        border-radius: 33px;
        overflow: hidden;
      }

      .contact {
        height: 47px;
        width: 156px;
        border: 1px solid $clr2;
        border-radius: 33px;
        background: $clr1;
        -webkit-mask: url('https://raw.githubusercontent.com/pizza3/asset/master/natureSmaller.png');
        mask: url('https://raw.githubusercontent.com/pizza3/asset/master/natureSmaller.png');
        -webkit-mask-size: 7100% 100%;
        mask-size: 7100% 100%;
        // border: none;
        color: $clr2;

        cursor: pointer;
        -webkit-animation: ani2 0.7s steps(70) forwards;
        animation: ani2 0.7s steps(70) forwards;

        &:hover {
          -webkit-animation: ani 0.7s steps(70) forwards;
          animation: ani 0.7s steps(70) forwards;
        }
      }
    }

    .cv {
      text-decoration: underline 2px solid $clr2;
      text-underline-offset: 5px;
    }
  }

  .photo {
    position: relative;
    width: 350px;
    height: 350px;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);

    img {
      opacity: 0.4;
    }

    .circle {
      position: absolute;
      width: 150px;
      height: 150px;
      border-radius: 50%;
      bottom: -75px;
      left: 50%;
      transform: translateX(-50%);
      z-index: 5;

      .text {
        font-family: plex-semibold;
        position: absolute;
        width: 100%;
        height: 100%;
        animation: rotateText 10s linear infinite;

        span {
          position: absolute;
          left: 50%;
          font-size: 1.2rem;
          transform-origin: 0 75px;
        }
      }
    }
  }

  @keyframes rotateText {
    0% {
      transform: rotate(360deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
}
